<template>
  <v-card style="cursor:pointer" :color="activeCard">
    <v-card-title class="text-no-wrap">{{ metricTitle }}</v-card-title>
    <v-tooltip bottom close-delay="250" open-delay="250">
      <template v-slot:activator="{ on, attrs }">
        <v-card-text
          v-bind="attrs"
          v-on="on"
          class="display-1 text-center pa-3 text-no-wrap text-truncate"
          :class="metricColor"
        >{{ metricValue }}</v-card-text>
      </template>
      <span v-text="metricValue"></span>
    </v-tooltip>
  </v-card>
</template>
<script>
export default {
  name: "dashboardCard",
  props: ["metricValue", "metricTitle", "metricColor", "selectedFilters"],
  computed: {
    activeCard: function() {
      return this.selectedFilters.includes(this.metricTitle.toLowerCase())
        ? "primary accent-4"
        : "";
    }
  }
};
</script>
