<template>
  <div>
    <v-card class="mt-2" style="position:relative" :color="cardStatusColor">
      <v-card-title class="primary--text justify-space-between d-flex">
        <div>{{ fullName }}</div>
        <div
          class="caption font-weight-bold"
          :class="{'green--text':cardStatus == 'Active', 'orange--text darken-5':cardStatus != 'Active'}"
        >{{ cardStatus }}</div>
      </v-card-title>
      <v-card-text>
        <div>
          Started:
          <b>{{ cardData.bill_start }}</b>
        </div>
        <div>
          Monthly Amount:
          <b>{{ cardData.monthly_amt }}</b>
        </div>
      </v-card-text>
      <v-card-actions class="text-right">
        <v-spacer></v-spacer>
        <v-btn icon color="primary">
          <v-icon>mdi-magnify</v-icon>
        </v-btn>
        <v-btn @click="removeConfirm(cardData.doc)" icon color="pink accent-3">
          <v-icon>mdi-trash-can</v-icon>
        </v-btn>
      </v-card-actions>
    </v-card>
    <template>
      <v-row justify="center">
        <v-dialog v-model="removeDialog" persistent max-width="290">
          <v-card>
            <v-card-title class="headline">Confirmation</v-card-title>
            <v-card-text>Are you sure you want to remove this Employee?</v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="dark" text @click="removeDialog = false">Cancel</v-btn>
              <v-btn color="pink accent-1" text @click="removeItem">Remove</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-row>
    </template>
  </div>
</template>
<script>
import { db } from "@/db";
export default {
  name: "eeCard",
  data: () => ({
    removePrep: "",
    removeDialog: false
  }),
  props: ["cardData"],
  computed: {
    fullName: function() {
      var self = this;
      return self.cardData.last_name + ", " + self.cardData.first_name;
    },
    cardStatus: function() {
      var self = this;
      return self.cardData.status == "active" ? "Active" : "Pending";
    },
    cardStatusColor: function() {
      var self = this;
      if (self.cardData.status == "active") {
        return "green lighten-5";
      } else {
        return "";
      }
    }
  },
  methods: {
    removeConfirm: function(r) {
      var self = this;
      self.removeDialog = true;
      self.removePrep = r;
    },
    removeItem: function() {
      var self = this;
      db.collection("testEEs")
        .doc(self.removePrep)
        .delete();
      self.removeDialog = false;
    }
  },
  watch: {
    removeDialog: function() {
      var self = this;
      if (!self.removeDialog) {
        self.removePrep = "";
      }
    }
  }
};
</script>
