<template>
  <div>
    <v-slide-y-transition leave-absolute mode="out-in">
      <template v-if="!loading">
        <v-container fluid style="position:relative">
          <v-row>
            <v-col cols="12" md="3">
              <dashboardCard
                @click.native="metricClick('active')"
                :metricValue="metricActive"
                :metricTitle="'Active'"
                :metricColor="'green--text'"
                :selectedFilters="selectedFilters"
              />
            </v-col>
            <v-col cols="12" md="3">
              <dashboardCard
                @click.native="metricClick('pending')"
                :metricValue="metricPending"
                :metricTitle="'Pending'"
                :metricColor="'orange--text'"
                :selectedFilters="selectedFilters"
              />
            </v-col>
            <v-col cols="12" md="3">
              <dashboardCard
                :metricValue="metricLatePay"
                :metricTitle="'Late Pay'"
                :metricColor="'red--text'"
                :selectedFilters="selectedFilters"
              />
            </v-col>
            <v-col cols="12" md="3">
              <dashboardCard
                :metricValue="metricMonthlyBillable"
                :metricTitle="'Monthly Billable'"
                :metricColor="''"
                :selectedFilters="selectedFilters"
              />
            </v-col>
          </v-row>
          <v-divider class="mt-5 mb-4"></v-divider>
          <v-slide-x-transition
            tag="div"
            class="row"
            group
            leave-absolute
            mode="out-in"
            style="position:absolute;left:12px;right:12px"
          >
            <v-col cols="12" md="3" v-for="ee in allEEFilter" :key="ee.doc">
              <eeCard :cardData="ee" />
            </v-col>
          </v-slide-x-transition>
          <v-scale-transition
            mode="out-in"
            tag="div"
            class="row justify-center"
            leave-absolute
            style="position:absolute"
          >
            <template v-if="allEEFilter.length == 0 && !loading">
              <div style="position:absolute;left:12px;right:12px;margin:auto">
                <v-col class="text-center">
                  <v-icon size="75px" color="primary">mdi-magnify</v-icon>
                  <div class="display-1 font-weight-light">Get started by choosing a status above.</div>
                </v-col>
              </div>
            </template>
          </v-scale-transition>
          <!-- <v-row>
			<v-col cols="12" md="9">
				<pendingSetupCard :pendingEEs="pendingEEs" />
			</v-col>
			<v-col cols="12" md="9">
				<activeSetupCard :activeEEs="activeEEs" />
			</v-col>
          </v-row>-->
        </v-container>
      </template>
    </v-slide-y-transition>

    <v-scale-transition leave-absolute>
      <template v-if="loading">
        <v-col class="text-center">
          <v-progress-circular size="70" width="7" color="purple" indeterminate></v-progress-circular>
          <div class="display-1 font-weight-light">Loading...</div>
        </v-col>
      </template>
    </v-scale-transition>
  </div>
</template>

<script>
export default {};
</script>

<style>
</style>

<script>
import axios from "axios";
import dashboardCard from "@/components/dashboardCard.vue";
// import pendingSetupCard from '@/components/pendingSetupCard.vue';
// import activeSetupCard from '@/components/activeSetupCard.vue';
import eeCard from "@/components/eeCard.vue";
export default {
  name: "Home",
  data: () => ({
    metrics: []
  }),
  props: ["activeEEs", "pendingEEs", "allEEs", "selectedFilters", "loading"],
  components: {
    dashboardCard,
    // pendingSetupCard,
    // activeSetupCard,
    eeCard
  },
  methods: {
    metricClick: function(r) {
      var self = this;
      var slicePos = self.selectedFilters.indexOf(r);
      console.log(slicePos);
      if (!self.selectedFilters.includes(r)) {
        self.selectedFilters.push(r);
      } else {
        self.selectedFilters.splice(slicePos, slicePos + 1);
      }
    },
    getMetrics: function() {
      var self = this;
      axios
        .get("/data/sampleMetrics.json")
        .then(function(response) {
          self.metrics = response.data;
        })
        .catch(function() {
          alert("fail");
        });
    }
  },
  computed: {
    allEEFilter: function() {
      var self = this;
      return self.allEEs.filter(function(r) {
        return self.selectedFilters.includes(r.status);
      });
    },
    metricActive: function() {
      var self = this;
      return self.allEEs.filter(function(x) {
        return x.status == "active";
      }).length;
    },
    metricPending: function() {
      var self = this;
      return self.allEEs.filter(function(x) {
        return x.status == "pending";
      }).length;
    },
    metricLatePay: function() {
      var self = this;
      return "3";
    },
    metricMonthlyBillable: function() {
      var self = this;
      return "$13,987.44";
    }
  },
  created: function() {
    this.getMetrics();
  }
};
</script>
